@import url('https://fonts.googleapis.com/css2?family=Jacquard+12&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&family=Jacquard+12&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.container {
    display: grid;
    grid-template-columns: 1fr 10fr;
    gap: 20px;
    background-color: #000000;
    width: 100%;
}

.colum1 {
    display: flex;
    width: 20px;
    height: 100%;
}
.colum2 {
    display: inline-flex;
    min-height: 100vh;
    top: 0;
    left: 0;
    flex-direction: column;
    color: white;
}

.wsp{
  font-family: "Anton", sans-serif;
  letter-spacing: 2px;
  justify-content:space-between;
  display: flex;
  align-items: center;
  background-color: green;
  height: 30px;
  width: 330px;
  padding: 15px;
  margin: 10px 10px 10px 0px;
  border-radius: 20px;
}

.img{
  height: 30px;
  width: 30px;
}

.App-link {
  font-family: "Anton", sans-serif;
  letter-spacing: 2px;
  text-align: center;
  background-color: #4e4e4e;
  height: 25px;
  width: 100px;
  padding: 10px;
  margin: 10px 10px 10px 0px;
  border-radius: 20px;
}

.My-Name {
    font-family: "Anton", sans-serif;
    font-size: 48px;
    letter-spacing: 5px;
    font-weight: 400;
    font-style: normal;
    text-align: left;
    margin: 0;
}

.My-Title {
    color: gold;
    font-weight: 400;
    font-style: normal;
    text-align: left;
    margin-bottom: 0;
}

.My-Text {
    color: grey;
    font-weight: 400;
    font-style: normal;
    text-align: left;
}
a {
  text-decoration: none; /* Quita el subrayado */
  color: inherit; /* Hereda el color del texto del elemento padre */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}